<template>
  <div class="container flex-fill d-flex flex-column my-3">
    <!-- spinner -->
    <div v-if="loading" class="spinner-border m-auto text-primary" role="status">
      <span class="sr-only">Carregando...</span>
    </div>
    <!-- palavra do presidente -->
    <div v-else-if="!loading && $store.state.presidente_texto" class="card">
      <div class="card-body">
        <!-- titulo -->
        <h5 class="text-center">PALAVRA DO PRESIDENTE</h5>
        <!-- texto -->
        <p class="text-justify" style="white-space: pre-wrap">{{$store.state.presidente_texto}}</p>
        <!-- autor -->
        <div class="text-center">
          <img v-if="$store.state.presidente_foto" :src="$store.state.presidente_foto" class="img-fluid presidente" alt="">
          <h5 v-if="$store.state.presidente_nome" class="mb-0">{{$store.state.presidente_nome}}</h5>
          <h6 class="text-primary">Presidente do CRECI-{{$store.getters.uf}}</h6>
        </div>
      </div>
    </div>
    <!-- falha -->
    <div v-else class="text-center flex-fill d-flex flex-column align-items-center justify-content-center">
      <i class="fas fa-exclamation-triangle fa-3x text-warning"></i>
      <p class="mt-2">Não há comunicado do presidente</p>
      <router-link class="btn btn-sm btn-secondary" :to="{ name: 'menu' }">voltar</router-link>
    </div>
  </div>
</template>

<script>
  export default {
    name: "PalavraCreci",
    data: () => ({
      loading: true
    }),
    async created() {
      try {
        await this.$store.dispatch('updatePresidenteData');
      } finally {
        this.loading = false;
      }
    }
  }
</script>

<style scoped>
  .presidente {
    border-radius: 50%;
    width: 8rem;
    height: 8rem;
    object-fit: cover;
    box-shadow: 0 2px 5px 0 rgba(0,0,0,.16), 0 2px 10px 0 rgba(0,0,0,.12);
  }
</style>
